.about {
  flex-direction: column;
  margin-top: 3em;
}

.about__name {
  color: var(--clr-primary);
}

.about__role {
  margin-top: 1.2em;
}

.about__desc {
  font-size: 1.1rem;
  max-width: 700px;
  line-height: 1.8;
  color: var(--clr-fg); /* Use your existing color variable */
  background: rgba(255, 255, 255, 0.03);
  padding: 1em 1.4em;
  border-left: 4px solid var(--clr-primary);
  border-right: 4px solid var(--clr-primary);
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  animation: fadeInLeft 1s ease-in-out forwards;
transform: translateX(-40px);
  opacity: 0;

}

@keyframes fadeInLeft {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.about__desc strong {
  color: var(--clr-primary);
}

.about__desc em {
  font-style: normal;
  color: var(--clr-secondary);
  font-weight: 600;
}


.about__desc,
.about__contact {
  margin-top: 2.4em;
}

.about .link--icon {
  margin-right: 0.8em;
}

.about .btn--outline {
  margin-right: 1em;
}

