.about__heading {
  font-size: 2rem;
  display: flex;
  align-items: center;
  gap: 0.4em;
}

.typed {
  display: inline-block;
  border-right: 2px solid var(--clr-primary);
  white-space: nowrap;
  overflow: hidden;
  width: 0;
  max-width: fit-content;
  animation: typing 2s steps(50, end) forwards, blink 0.75s step-end 1s 3;
}


@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

@keyframes blink {
  from,
  to {
    border-color: transparent;
  }
  50% {
    border-color: var(--clr-primary);
  }
}
.link--btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5em;
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  padding: 0.6em 1.2em;
  border-radius: 4px;
  transition: background 0.3s, color 0.3s;
  text-decoration: none;
  font: inherit;
}

.link--btn:hover,
.link--btn:focus,
.link--btn:active {
  background: var(--clr-primary);
  color: var(--clr-bg);
}

.btn__icon {
  font-size: 1.2rem;
  
  vertical-align: middle;
  transition: color 0.3s;
}

.link--btn:hover .btn__icon,
.link--btn:focus .btn__icon,
.link--btn:active .btn__icon {
  color: var(--clr-bg);
}
